const html = '\
<div  id=\'write\'  class = \'\'><h1><a name="display-float-position" class="md-header-anchor"></a><span>display-float-position</span></h1><h2><a name="display" class="md-header-anchor"></a><span>display</span></h2><p><span>1、</span><code>none</code><span>设置元素不可⻅</span></p><p><span>2、</span><code>block</code><span>转换成块状元素。</span></p><p><span>3、</span><code>inline</code><span>转换成⾏内元素。 </span></p><p><span>4、</span><code>inline-block</code><span> 象⾏内元素⼀样显示，但其内容象块类型元素⼀样显示。 </span></p><p><span>5、 </span><code>table</code><span>此元素会作为块级表格来显示 </span></p><p><span>6、</span><code>inherit</code><span>规定应该从⽗元素继承 </span><code>display</code><span>属性的值</span></p><h2><a name="position" class="md-header-anchor"></a><span>position</span></h2><p><span>1、</span><code>static</code><span>默认值。没有定位，元素出现在正常的流中 </span></p><p><span>2、</span><code>relative</code><span>：⽣成相对定位的元素，相对于其正常位置进⾏定位 </span></p><p><span>3、</span><code>absolute</code><span>：⽣成绝对定位的元素，相对于 </span><code>static</code><span>定位以外的第⼀个⽗元素进⾏定位 </span></p><p><span>4、</span><code>fixed</code><span>：⽣成绝对定位的元素，相对于浏览器窗⼝进⾏定位</span></p><p><span>5、</span><code>sticky</code><span>：粘性定位，该定位基于用户滚动的位置。</span></p><ul><li><span>未超出目标区域，行为像position: relative</span></li><li><span>超出目标区域，行为像position: fixed</span></li></ul><p><span>6、</span><code>inherit</code><span>规定从⽗元素继承 </span><code>position</code><span>属性的值</span></p><h2><a name="三者关系" class="md-header-anchor"></a><span>三者关系</span></h2><ol start=\'\' ><li><span>如果 </span><code>display</code><span>取值为 </span><code>none</code><span>，那么 </span><code>position</code><span>和 </span><code>float</code><span>都不起作⽤</span></li><li><code>display</code><span>非 </span><code>none</code><span>，且如果 </span><code>position</code><span>取值为 </span><code>absolute</code><span>或者 </span><code>fixed</code><span>，框就是绝对定位的， </span><code>float</code><span>的计 算值为 </span><code>none</code><span>， </span><code>display</code><span>根据下⾯的表格进⾏调整。 </span></li><li><code>display</code><span>非 </span><code>none</code><span>， </span><code>position</code><span>不是绝对定位，且如果 </span><code>float</code><span>不是 </span><code>none</code><span>，框是浮动的， </span><code>display</code><span>根据下表进⾏调整 </span></li><li><span>如果元素是根元素， </span><code>display</code><span>根据下表进⾏调整 </span></li><li><span>其他情况下 </span><code>display</code><span>的值为指定值 </span></li></ol><figure><table><thead><tr><th><span>指定值</span></th><th><span>计算值</span></th></tr></thead><tbody><tr><td><span>inline-table</span></td><td><span>table</span></td></tr><tr><td><span>inline, table-row-group, table-column, table-column-group, table-header-group, table-footer-group, table-row, table-cell, table-caption, inline-block</span></td><td><span>block</span></td></tr><tr><td><span>other</span></td><td><span>不变</span></td></tr></tbody></table></figure></div>\
'; export default html